@import 'varibles'
@import 'animations'
@import 'darkmode'
@import 'pc_e_lampada'

body
    margin: 0
    padding: 0
    height: 100vh



.container-navbar
    position: fixed
    width: 60px
    height: 100vh 
    display: flex
    flex-direction: column
    justify-content: center
    gap: 50px
    transition: width 0.6s ease
    background: #293431
    overflow: hidden
    z-index: 999999

    &:hover
        width: 190px

    .navbar
        position: relative
        color: #fff
        cursor: pointer
        display: flex
        align-items: center
        white-space: nowrap
        left: 20px
        transition: transform 0.6s ease, opacity 0.6s ease


        &:not(:last-child):hover
            transform: scale(1.1) 
            transition: 0.2s

        &:not(:last-child):hover::before
            content: ''
            position: absolute
            left: -10px
            width:88%
            height: 40px
            background: #7a0808
            border-radius: 50px
            
                   
        .fa-solid
            font-size: 1.3em
            transform: translateX(0)
            transition: transform 0.6s ease

        a
            margin-left: 10px
            text-decoration: none
            color: #fff
            font-family: $fontNoto
            font-size: 1.2em
            opacity: 0
            transform: translateX(-20px)
            transition: opacity 0.6s ease, transform 0.6s ease

    &:hover .navbar a
        opacity: 1
        transform: translateX(10%)

    &:hover .navbar .fa-solid
        transform: translateX(0)



.darkmode
    position: relative 
    top: 20%    
    width: 0


    & .btn-container
        position: absolute
        display: flex
        align-items: center
        transform: translate(20px)
        width: 60px
        height: 30px
        border-radius: 20px
        background-color: #ffffff79
        opacity: 0
        transition: opacity 0.6s ease, transform 0.6s ease


        & button
            position: absolute
            left: 5px
            width: 20px
            height: 20px
            border-radius: 50%
            cursor: pointer
            transition: opacity 0.6s ease, transform 0.6s ease
            

.container-navbar:hover .btn-container
    transform: translate(40px)
    opacity: 1
    transition: opacity 0.6s ease, transform 0.6s ease


.container-apresentacao
    position: relative
    top: 10%
    right: -50%
    width: auto
    max-width: 30%
    height: auto
    white-space: nowrap
    color: #FAF3E0
    font-size: 2em
    font-family: $fontItim  
    z-index: 1

    & img
        position: absolute
        top: -60%
        right: -20%
        width: 30%
        z-index: -99

    & h1
        
        display: block
        animation: fade-in-slide 1.5s ease-out both

    & p
        display: block
        margin-top: 10px
        animation: zoom-in-tracking 1.5s ease-out both 1s 
        z-index: 1
    

.click-container
    position: fixed
    width: 100%
    height: 100%
    pointer-events: none
    z-index: 9999999999999999999999
    top: 0
    left: 0

    .splash
        position: absolute
        animation: fadeOut 2s forwards
        opacity: 1
        transform: scale(1)

        & img
            width: 50px 
            height: 50px 
            pointer-events: none 
            color: #fff