@keyframes digitacao
    0%
        width: 0
    100%
        width: 100%    

@keyframes cursorPiscando 
    50% 
        border-color: transparent
    
    100% 
        border-color: #fff 

    
@keyframes falhaLuz
    0% 
        background-color: #00000000 
        opacity: 0

    10%
        background-color: #6b6448 
        opacity: 0.8

    20%
        background-color: #6b6448
        opacity: 0.4

    30%
        background-color: #6b6448
        opacity: 1

    40%
        background-color: #00000000 
        opacity: 0

    50%
        background-color: #6b6448
        opacity: 0.6

    60%
        background-color: #6b6448
        opacity: 0.2

    70%
        background-color: #6b6448
        opacity: 1

    80%
        background-color: #6b6448
        opacity: 0.8

    90%
        background-color: #6b6448
        opacity: 0.5

    100%
        background-color: #6b6448 
        opacity: 1


@keyframes fade-in-slide
    0%
        opacity: 0
        transform: translateX(100px)
    100%
        opacity: 1
        transform: translateX(0)

@keyframes zoom-in-tracking
    0%
        transform: scale(0.8)
        letter-spacing: -0.5em
        opacity: 0
    50%
        transform: scale(1.1)
        letter-spacing: 0.2em
        opacity: 0.7
    100%
        transform: scale(1)
        letter-spacing: 0
        opacity: 1

@keyframes bounce-in
    0%
        transform: scale(0.5)
        opacity: 0
    50%
        transform: scale(1.2)
        opacity: 0.8
    100%
        transform: scale(1)
        opacity: 1


@keyframes fadeOut 
    0% 
      opacity: 1
      transform: scale(1)
    
    100% 
      opacity: 0
      transform: scale(1.2)
    
  