.carcaca-pc
    display: flex
    justify-content: center
    align-items: center
    position: relative
    left: 20%
    top: 348px
    width: 300px
    height: 160px   
    background-color: #211f1f
    border-radius: $bordaAcimaMac
    

    &::before
        content: ''
        display: flex
        justify-content: center
        align-items: center
        background-color: #fff
        width: 100%
        height: 15%
        left: 0
        position: absolute
        bottom: -25px
        border-radius: $borderAbaixoMac
        border: 1px solid #000

    &::after
        content: ''
        position: absolute
        bottom: -20px  
        height: 15px
        width: 15px
        border-radius: 25px
        background-color: #000000 
        z-index: 999999

    & .tela
        width: 93%
        height: 88%

.tela img
    position: relative
    z-index: 99999
        

.base
    position: absolute
    bottom: -34px

    &::before
        content: ''
        position: absolute
        clip-path: polygon(7% 0, 94% 0, 100% 100%, 0% 100%)
        top: -10px
        left: 50%
        transform: translateX(-50%)
        width: 60px
        height: 25px
        background-color: #333333c2

    &::after 
        content: ''
        position: absolute
        transform: translate(-50%)
        top: 15px
        width: 90px
        height: 5px
        background-color: #5d5858
        border-radius: 20px
   



.container-lampada
    position: relative
    left: 23% // Centraliza horizontalmente
    transform: translateX(-50%) // Ajusta para centralizar corretamente
    top: 0

.haste-lampada
    position: absolute
    display: flex
    justify-content: center
    align-items: center
    left: 50% // Centraliza dentro do contÃªiner
    width: 4px
    height: 120px
    background-color: #005431
    border: 2px solid #13a254cb
    border-top: 0px
    border-bottom: 0px

    &::before
        content: ''
        width: 12px
        height: 10px
        background-color: #005431 
        position: absolute
        bottom: 0
        border: 2px solid #13a254cb
        border-bottom: 0px
        border-radius: 20px 20px 0px 0px

    &::after
        content: ''
        width: 30px
        height: 30px
        background-color:  #005431 
        position: absolute
        bottom: -28px
        border-radius: 20px 20px 0px 0px
        border: 2px solid #13a254cb
        border-bottom: 0px

.bocal-lampada
    content: ''
    width: 80px
    height: 50px
    background-color: #005431 
    position: absolute
    bottom: -75px
    border-radius: 40px 40px 0px 0px
    border: 2px solid #13a254cb
    border-bottom: 0px
    display: flex
    justify-content: center 

    &::before 
        content: ''
        width: 100px
        height: 10px
        border-radius: 20px
        position: absolute
        bottom: 0
        background-color: #037646 
        border: 2px solid #13a254cb
        z-index: 99999

.brilho-luz
    width: 400px
    height: 370px
    background-color: #6b6448
    position: absolute
    bottom: -23em
    clip-path: polygon(43% 0, 57% 0, 100% 100%, 0% 100%)
    animation: falhaLuz 0.5s ease-in-out forwards
    z-index: -999
    
    

.luz
    content: ''
    position: absolute
    bottom: -15px
    width: 40px
    height: 20px
    background-color: #ffee00
    border-radius: 0px 0px 20px 20px
    z-index: -2

.prateleira-pc
    position: absolute  
    bottom: -70px
    border-radius: 25px
    width: 40em
    height: 15px
    background-color: #642d0a

    & #flor-mario    
        position: absolute
        right: 140px
        bottom: 11px
        
        

    & #bloco-mario
        position: absolute
        bottom: 8px
        left: 90px
      
